import React from "react";
import './nav-bar.scss';
import avatar from '../../img/avatar.jpg';
import {NavLink} from "react-router-dom";
import Footer from "./footer";

function NavBar () {
    return (
        <aside className='nav-bar__inner'>
            <div className="nav-bar__avatar-container">
                <img className="nav-bar__avatar" src={avatar} alt="avatar"/>
            </div>
            <nav className="nav-bar__navigation">
                <NavLink className="nav-bar__navigation-item" to="/dh-portfolio/" exact activeClassName="active">
                    Главная
                </NavLink>
                <NavLink className="nav-bar__navigation-item" to="/dh-portfolio/about" exact activeClassName="active">
                    Обо мне
                </NavLink>
                {/*<NavLink className="nav-bar__navigation-item" to="/dh-portfolio/portfolio" exact activeClassName="active">*/}
                {/*    Портфолио*/}
                {/*</NavLink>*/}
                <NavLink className="nav-bar__navigation-item" to="/dh-portfolio/achievements" exact activeClassName="active">
                    Достижения
                </NavLink>
                <NavLink className="nav-bar__navigation-item" to="/dh-portfolio/contacts" exact activeClassName="active">
                    Контакты
                </NavLink>
            </nav>
            <footer className="footer">
                <Footer/>
            </footer>
        </aside>
    )
}

export default NavBar;