import React from 'react';
import '../styles/_achievements.scss';
import Title from "../components/title";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faTrophy} from "@fortawesome/free-solid-svg-icons";

function Achievements() {
    return (
        <section className="achievements-page">
            <div className="container">
                <Title header={'Мои достижения'} reflection={'Мои достижения'}/>
                <div className="achievements-page__list">
                    <FontAwesomeIcon icon={faTrophy} className="achievements-page__list-item"/>
                    <span className="achievements-page__list-year">2017</span>
                    <span className="achievements-page__list-text">Закончил Алмаатинский колледж "Престиж" и получил
                    диплом по специальности "техник-программист".</span>
                </div>
                <div className="achievements-page__list">
                    <FontAwesomeIcon icon={faTrophy} className="achievements-page__list-item"/>
                    <span className="achievements-page__list-year">2019</span>
                    <span className="achievements-page__list-text">Во время прохождения срочной службы получил от
                    командира войсковой части благодарственную грамоту за добросовестное выполнение воинского долга.
                    </span>
                </div>
                <div className="achievements-page__list">
                    <FontAwesomeIcon icon={faTrophy} className="achievements-page__list-item"/>
                    <span className="achievements-page__list-year">2019</span>
                    <span className="achievements-page__list-text">По окончанию прохождения срочной службы получил звание
                        младшего сержанта и положительную характеристику от ВрИО командира роты.
                    </span>
                </div>
                <div className="achievements-page__list">
                    <FontAwesomeIcon icon={faTrophy} className="achievements-page__list-item"/>
                    <span className="achievements-page__list-year">2019</span>
                    <span className="achievements-page__list-text">Поступил на второй курс дистанционного обучения, на
                        факультет информатики и вычислительной техники в Сибирский Государственный Университет
                        Телекоммуникаций и Информатики. В данный момент нахожусь на 3-ем курсе.
                    </span>
                </div>
                <div className="achievements-page__list">
                    <FontAwesomeIcon icon={faTrophy} className="achievements-page__list-item"/>
                    <span className="achievements-page__list-year">2020</span>
                    <span className="achievements-page__list-text">На локальных соревнованиях по программированию
                        "Хакатон ДНС 2020" со своей командой занял первое место в номинации "Самое креативное решение".
                    </span>
                </div>
            </div>
        </section>
    )
}

export default Achievements;