import React from "react";
import Title from "../components/title";
import ImageSlider from "../components/image-slider";
import '../styles/_about.scss';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import resume from "../components/Хаустов Денис.pdf"
import {
    faBootstrap,
    faCss3,
    faHtml5,
    faJsSquare,
    faLess,
    faPhp,
    faReact,
    faSass,
    faVuejs
} from "@fortawesome/free-brands-svg-icons";
import {Link} from 'react-router-dom';
import {faDatabase} from "@fortawesome/free-solid-svg-icons";

function About() {
    return (
        <div className="about-page">
            <div className="container">
                <Title header={'Обо мне'} reflection={'Обо мне'}/>
                <div className="about-page__main-info">
                    <ImageSlider/>
                    <div className="about-page__info">
                        <h2 className="about-page__info-header">Подробная <span
                            className='about-page__header-inner'>информация:</span></h2>
                        <p className="about-page__info-text">Студент 3-го курса дистанционного обучения.
                            В свободное от работы и учёбы время люблю читать книги, играть в компьютерные игры,
                            занимаюсь
                            спортом.
                        </p>
                        <div className="about-page__table">
                            <span className="about-page__table-item blue">Полное имя:</span>
                            <span className="about-page__table-item">Хаустов Денис <span className="about-page__item-other">
                                (да-да, без отчества, не удивляйтесь)</span>;
                            </span>

                            <span className="about-page__table-item blue">Дата рождения:</span>
                            <span className="about-page__table-item">19.05.1997;</span>

                            <span className="about-page__table-item blue">Любимый я.п.:</span>
                            <span className="about-page__table-item">Java Script;</span>

                            <span className="about-page__table-item blue">Считаю главным:</span>
                            <span className="about-page__table-item">Упорство, вера в победу, трудолюбие.</span>
                        </div>
                        <div className="about-page__btn-container">
                           <Link className="about-page__btn" to={resume} target="_blank">
                               Посмотреть полное резюме
                           </Link>
                            <Link className="about-page__btn-mobile" to={resume} target="_blank">
                                Посмотреть резюме
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="about-page__skills">
                    <Title header={'Hard skills'} reflection={'Hard skills'}/>
                    <div className="about-page__skills-container">
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faHtml5} className="about-page__skills-item"/>
                            <p className="about-page__skills-description">
                                Знаю что такое объектная модель документа (DOM), дочерние и родительские элементы.
                                Использую на практике тэги всех версий языка гипер-текстовой разметки.
                            </p>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faCss3} className="about-page__skills-item"/>
                            <div className="about-page__skills-group">
                                <p className="about-page__skills-description">
                                    Именую классы по БЭБ. Довольно глубоко изучил Каскадные Таблицы Стилей, стараюсь
                                    использовать
                                    весь спектр предоставляемых возможностей, таких как:
                                </p>
                                <ul className="about-page__skills-list">
                                    <li className="about-page__list-item">Наследование и каскад;</li>
                                    <li className="about-page__list-item">Группировка селекторов, комбинаторы;</li>
                                    <li className="about-page__list-item">Селекторы псевдоклассов;</li>
                                    <li className="about-page__list-item">Анимации, градиенты, переходы,
                                        трансформации;
                                    </li>
                                    <li className="about-page__list-item">Часто использую Flex-Box и Grid;</li>
                                    <li className="about-page__list-item">Адаптивная, кросс-браузерная, mobile-first
                                        вёрстка
                                        и т.д.
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faBootstrap} className="about-page__skills-item"/>
                            <p className="about-page__skills-description">
                                Первый "фреймворк", который я стал использовать в повседневных задачах на первом месте
                                работы.
                                Очень нравится отзывчивость Бутстрапа и его простота в использовании при помощи
                                классов.
                                Но
                                есть и вторая сторона - это его вес. Не всегда уместно использовать данный
                                фреймворк,
                                если
                                нужна быстрота и скорость загрузки страниц.
                            </p>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faJsSquare} className="about-page__skills-item"/>
                            <div className="about-page__skills-group">
                                <p className="about-page__skills-description">
                                    Особенный язык программирования. Особенный он по тому что в нём нет строгой
                                    типизации
                                    (из-за этого был создан TypeScript) и есть приведение типов, из-за чего язык
                                    больше
                                    используется
                                    для функционального программирования, а не объектно-ориентированного. Из данного
                                    языка
                                    программирования я знаю:
                                </p>
                                <ul className="about-page__skills-list">
                                    <li className="about-page__list-item">Типы переменных, данных, массивы.
                                        Преобразование
                                        типов;
                                    </li>
                                    <li className="about-page__list-item">Условное ветвление, логические операторы,
                                        циклы;
                                    </li>
                                    <li className="about-page__list-item">Функции, анонимные функции, стрелочные
                                        функции;
                                    </li>
                                    <li className="about-page__list-item">Получение и сохранение данных в localStorage;
                                    </li>
                                    <li className="about-page__list-item">Элементы DOM дерева: навешивать события,
                                        изменять элементы DOM с помощью Js;
                                    </li>
                                    <li className="about-page__list-item">Различия между Get и Post запросами;
                                    </li>
                                    <li className="about-page__list-item">Импорт/экспорт данных, промисы.</li>
                                </ul>
                                <br/>
                                Кроме функционального Js, я знаком так же с объектно-ориентированным TypeScript.
                                Знаю
                                что
                                такое классы, объекты и как с ними работать. Понимание данного языка далось мне
                                легко,
                                т.к. он во многом похож на PHP из мира Js, с которым я знаком с начала своей
                                карьеры,
                                хотя
                                углубленного изучения TypeScript у меня не было.
                            </div>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faReact} className="about-page__skills-item"/>
                            <div className="about-page__skills-group">
                                <p className="about-page__skills-description">
                                    Писал на данном фреймворке 2 личных проекта (на одном из них вы сейчас
                                    находитесь).
                                    В
                                    перспективе
                                    развития React у меня находится на первом месте, т.к. является наиболее
                                    востребованной
                                    технологией.
                                    Из данного фреймворка я знаю:
                                </p>
                                <ul className="about-page__skills-list">
                                    <li className="about-page__list-item">Общую структуру фреймворка;</li>
                                    <li className="about-page__list-item">NavLink, Route, создание компонента;</li>

                                </ul>
                            </div>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faVuejs} className="about-page__skills-item"/>
                            <p className="about-page__skills-description">
                                Vue в моём "топе" находится на втором месте, т.к. является вторым фреймворком по
                                популярности после React. Сталкивался с ним в повседневных задачах, в основном
                                использовал v-if, промисы, знаком со структурой vue проекта, изучал хуки жизненного цикла.
                                <br/>
                                Так же на втором месте работы разрабатывал фронт с минималистичным фреймворком на основе
                                Vue - Nuxt. По сути тот же Vue, только в другой обложке и меньше.
                            </p>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faSass} className="about-page__skills-item"/>
                            <div className="about-page__skills-group">
                                <p className="about-page__skills-description">
                                    Хорошо знаком с препроцессором Sass и SCSS. Но предпочитаю больше второй, т.к.
                                    он наиболее читаемый. Знаю как использовать:
                                </p>
                                <ul className="about-page__skills-list">
                                    <li className="about-page__list-item">Переменные SCSS, миксины, импорт;</li>
                                    <li className="about-page__list-item">Условная конструкция @if;</li>
                                    <li className="about-page__list-item">Циклы @for, @each, @while, функции.</li>
                                </ul>
                            </div>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faLess} className="about-page__skills-item"/>
                            <p className="about-page__skills-description">
                                Использовал Less в одном из своих проектов в связке с Gulp, когда SCSS был не так
                                популярен. Если
                                сравнивать два данных препроцессора, то Less окажется сложнее. В нём используется
                                знак @ для определения переменных, в то время, как в SCSS знак $. Так же вычисления
                                в Less нелогичные, допустим если 100% - 50px, то в итоге мы получим 50%. Помимо этого
                                есть ещё проблемы, которые делают его ниже, чем SCSS в глазах разработчиков.
                            </p>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faPhp} className="about-page__skills-item"/>
                            <div className="about-page__skills-group">

                                <p className="about-page__skills-description">
                                    Самый нелюбимый серверный язык программирования многих разработчиков (нелюбимый мной
                                    в
                                    том числе). Использовал я его на первом месте работы (Bitrix под управлением PHP) и
                                    на
                                    втором фреймворк Yii 2. По началу изучение данного я.п. далось мне немного
                                    сложновато,
                                    т.к. до него я изучал Js и считал Php ненужным языком. Но со временем свыкся и стал
                                    больше уделять ему времени. Из данного языка я знаю:
                                </p>
                                <ul className="about-page__skills-list">
                                    <li className="about-page__list-item">Типы данных, массивы, классы,
                                        объекты, наследование;
                                    </li>
                                    <li className="about-page__list-item">Условное ветвление, логические операторы,
                                        циклы;
                                    </li>
                                    <li className="about-page__list-item">Функции, анонимные функции, стрелочные
                                        функции;
                                    </li>
                                    <li className="about-page__list-item">Различия между Get и Post запросами;
                                    </li>
                                    <li className="about-page__list-item">Большую часть магических методов;
                                    </li>
                                    <li className="about-page__list-item">Знаком с объектно-ориентированной частью
                                        языка;
                                    </li>
                                    <li className="about-page__list-item">Изучал абстрактную фабрику, одиночку, адаптер.
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="about-page__skills-wrapper">
                            <FontAwesomeIcon icon={faDatabase} className="about-page__skills-item"/>
                            <p className="about-page__skills-description">
                                Ну и на конец последнее в моём списке - это базы данных. Работал с такими бд, как
                                MySQL, PostgreSQL. Особо ни с чем сложным не сталкивался, подключался к бд, делал простые
                                запросы через PHP, добавлял информацию, делал выборку, удалял данные, разрывал соединение.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About;