import React from 'react'

function ContactItem({icon, text, title}) {
    return (
        <div className="contact-page__item">
            <div className="contact-page__item-wrapper">
                <div className="contact-page__item-img">{icon}</div>
                <div className="contact-page__item-info">
                    <h4 className="contact-page__item-title">{title}</h4>
                    <p className="contact-page__item-text">{text}</p>
                </div>
            </div>
        </div>
    )
}

export default ContactItem;
